* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    background: linear-gradient(
        to top,
        #d3cce3,
        #e9e4f0
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    min-height: calc(100vh - 35px);
    font: 16px;
    font-family: sans-serif;
}
h2 {
    color: crimson;
    margin-bottom: 25px;
}
